/**
 * @module 【plugin】SessionHelper
 * @desc 会话级数据Session帮助类
 * （主要是获取和设置Session数据）
 */
export default {
  /**
   * @func getSessionDataAsync
   * @desc 获取session
   * @example import { SessionHelper } from 'awp-plugin-session-helper'
   *
   * SessionHelper.getSessionDataAsync()
   */
  getSessionDataAsync() {
    return new Promise((resolve, reject) => {
      cordova.exec(result => {
        resolve(result);
      }, error => reject(error), 'SessionHelper', 'GetSessionData');
    });
  },
  /**
   * @func setSessionDataAsync
   * @desc 设置session
   * @param {string} sessionData - 要设置的session
   * @example import { SessionHelper } from 'awp-plugin-session-helper'
   *
   * SessionHelper.setSessionDataAsync(sessionData)
   */
  setSessionDataAsync(sessionData) {
    return new Promise((resolve, reject) => {
      if (!sessionData) {
        reject('[SessionHelper] sessionData invalid');
      };
      cordova.exec(result => resolve(result),
        error => reject(error), 'SessionHelper', 'SetSessionData', [sessionData]);
    });
  }
};
